




















































































































import goodsCard from 'template/goods_card'
import loading from 'template/loading'
export default {
	components: {
		goodsCard,
		loading
	},
	data() {
		return {
			popUp: false,
			isInvoice: false,
			order: {},
			goods: [],
			address: {},
			remark: '',
			isUpdate: false,
			invoiceType: 1,
			invoiceData: {},
			invoiceText: '本次不开具发票',
			isLoading: false,
		};
	},
	onShow() {
		if (this.isUpdate) {
			this.getOrderInfo()
		}
	},
	onLoad() {
		this.getOrderInfo()
	},
	methods: {
		getOrderInfo() {
			var that = this
			this.isLoading = true
			this.$api.post('/api/order/cartOrder', {
				address_id: this.address.id,
				invoice_info: ''
			}, function(res) {
				console.log(res)
				that.isLoading = false
				if (res.data.code == 200) {
					that.order = res.data.data
					that.goods = res.data.data.goods_info
					console.log(res.data.data.address)
					that.address = res.data.data.address
				} else {
					uni.showToast({
						title: res.data.msg,
						mask: true,
						icon: 'none'
					});
				}
			})
		},


		//生成订单
		generateOrder() {
			if (!this.address.id) {
				return uni.showToast({
					title: '请添加一个地址',
					icon: 'none'
				})
			}
			var that = this
			// 				var para = {
			// 					
			// 				}
			this.$api.post('/api/order/generateOrder', {
				pay_way: 1,
				coin: "",
				address_id: this.address.id,
				remark: this.remark,
				invoice_info: this.invoiceData.title ? JSON.stringify(this.invoiceData) : ''
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					var order_id = res.data.data.order_id
					that.doPay(order_id)
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},

		//支付
		doPay: function(order_id) {
			var that = this
			this.$api.post('/api/miniprogram.payment/unifiedOrder', {
				order_id: order_id
			}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					var payObj = res.data.data;
					payObj.success = function() {
						//todo 这里写支付成功后的逻辑操作
						that.isUpdate = false
						setTimeout(function() {
							uni.redirectTo({
								url: 'pay_success/pay_success?id=' + order_id,
							})
						}, 300)
					}
					payObj.fail = function() {
						//todo 这里写取消支付的逻辑
						uni.showToast({
							title: '您取消了支付',
							icon: 'none'
						})
						setTimeout(function() {
							uni.redirectTo({
								url: '/pages/user/orders/order-detail/order-detail?id=' + order_id,
							})
						}, 500)
					}
					uni.requestPayment(payObj)
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}

			})
		},

		//提交发票信息
		formSubmit(e) {
			console.log(e.detail.value)
			var form = e.detail.value
			var data = {
				invoice_type: 1,
				title_type: this.invoiceType,
				title: form.title,
				tax_no: form.tax_no,
				bank: form.bank,
				bank_no: form.bank_no,
				address: form.address,
				phone: form.phone
			}
			console.log(data)
			if (this.invoiceType == 1) {
				if (data.title == '' || data.tax_no == '') {
					return uni.showToast({
						title: '星号项为必填项',
						icon: 'none'
					})
				}
			}
			if (this.invoiceType == 2) {
				if (data.title == '') {
					return uni.showToast({
						title: '星号项为必填项',
						icon: 'none'
					})
				}
			}

			this.invoiceData = data
			this.invoiceText = this.invoiceType == 1 ? '企业发票' : '个人发票'
			this.hidePop()
		},

		//不使用发票
		noInvoice() {
			this.invoiceData = {}
			this.hidePop()
			this.invoiceText = '本次不开具发票'
		},

		//发票弹窗
		setInvoice() {
			this.isInvoice = !this.isInvoice
		},

		//发票类型
		changeInvType(op) {
			this.invoiceType = op
			console.log(this.invoiceType)
		},
		//留言
		getRemark(e) {
			this.remark = e.detail.value
		},

		hidePop() {
			this.popUp = false
			this.isInvoice = false
		}
	}
}
